.hero {
  position: relative;
  z-index: 0;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: $black;
    opacity: 0.2;
  }

  &:after {
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    background: linear-gradient(
      to top,
      rgba(0, 0, 0, 1) 0%,
      rgba(0, 0, 0, 0) 20%
    );
    z-index: 2;
  }

  &__bg-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    object-fit: cover;
    height: 100%;
    opacity: 0;
    transition: 1s ease;

    &.active {
      opacity: 1;
    }
  }
}

// WELCOME TEXT
.welcome-text {
  position: relative;
  width: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
  flex: 2;
  z-index: 3;

  @media (min-width: $screen-sm) {
    flex-flow: row;
  }

  &--content {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-flow: column;
    color: $white;
    font-size: 2rem;
    flex: 1;
    padding: 1rem;
    animation: mainContentFadeIn 1s ease-out;

    @keyframes mainContentFadeIn {
      from {
        opacity: 0;
        margin-top: -200px;
      }
      to {
        opacity: 1;
        margin-top: 0;
      }
    }

    @media (min-width: $screen-sm) {
      font-size: 3rem;
    }
    p {
      font-weight: $fontWeightSemi;
    }
  }

  &--logo {
    @media (min-width: $screen-sm) {
      flex: 1;
    }
  }
}

